@use 'base/variables' as v;
@use 'base/mixins' as m;
@use "sass:color";

.login{
    margin-top: 5rem;
    .uptask{
        @include m.texto-degradado(v.$cyan);

    }

    input[type="submit"]{
        @include m.boton(v.$cyan, v.$blanco);
    }

    .acciones{
        a{
            color: v.$cyan;
        }
    }

}