@use 'base/variables' as v;
@use 'base/mixins' as m;
@use "sass:color";

.formulario{
    margin-top: 5rem;

    .campo{
        font-weight: bold;
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        label{
            flex: 0 0 10rem;

        }
        input{
            flex: 1;
            padding: 1rem;
            border: none;
            background-color: color.adjust(v.$gris, $lightness: 40%)

        }

    }

}