@use 'base/variables' as v;
@use "sass:color";

.alerta{
    padding: 1rem;
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    max-width: 80rem;

    &.error{
        background-color: color.adjust(v.$rojo, $lightness: 60%);
        border-left: .5rem solid v.$rojo;
        color: v.$rojo;
    }

    &.exito{
        background-color: color.adjust(v.$verde, $lightness: 60%);
        border-left: .5rem solid v.$verde;
        color: v.$verde;
    }
}