@use 'base/variables' as v;
@use 'base/mixins' as m;
@use "sass:color";

.restablecer{
    margin-top: 5rem;
    .uptask{
        @include m.texto-degradado(v.$azul);

    }

    input[type="submit"]{
        @include m.boton(v.$azul, v.$blanco);
    }

    .acciones{
        a{
            color: v.$azul;
        }
    }

}